.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}
.nav-item ul{
    display: flex;
    list-style-type: none;
}
.nav-item ul li{
    padding: 10px;
    margin-top: 20px;
}
.lineremove{
    text-decoration: none;
    color: black;

}
.logo{
    width: 100px;
}
.card{
   
    width: 300px;
    height: auto; 
    border: 1px solid black;
    display: flex;
    flex-direction: column; 
    justify-content: space-between; 
    padding: 10px; 
    box-sizing: border-box; 
    overflow-wrap: break-word;


}


.allcards{
    display: flex;
    flex-wrap: wrap;
    gap: 75px;
    margin-left: 44px;
    overflow-wrap: break-word;
   
}
.card img{
    width: 100%;
    height: 150px;
    object-fit: cover;
}
.card .card-body {
    display: flex;
    flex-direction: column;
    gap: 10px; 
    text-align: center; 
}

.card .card-address {
    font-size: 14px; 
    color: gray; 
    word-wrap: break-word;
    margin-top: 10px;
}
.searchBar{
    margin: 20px;

}
.shimmer{
    display: flex;
    gap: 75px;
    flex-wrap: wrap;
}
.box{
    width: 300px;
    height: 300px;
    border: 1px solid black;
   

}
.menudata img{
    width: 500px;
    height: 300px;

}
.menudata {
    display: block;

}
.button{
    margin: 28px;
   
}
.nick{
    text-decoration: none;
    color: black;
    display: flex;
    flex-wrap: wrap;
 
  
}

